	export default {
		data() {
			return {
				authKey:'',
				nameValue:'',
				phoneValue:'',
				streeValue:'',
				doorValue:'',
				jid:'',
				biId:'',
				address:[],
			};
		},
		onLoad(e){
			this.jid = e.jid
			this.biId = e.id
			this.authKey = uni.getStorageSync('authKey');
			uni.setStorageSync('address', '')
			this.getAdress();
		},
		methods:{
			// 获取地址
			getAdress(){
				uni.request({
					url: this.http+'home/getAddressList',
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {},
					success: res => {
						console.log(res.data.data)
						if (res.data.code == 200) {
							this.address = res.data.data
						}
					},
				});
			},
			nameInput: function(e) {
				this.nameValue = e.target.value;
			},
			phoneInput:function(e) {
				this.phoneValue = e.target.value;
			},
			streeInput:function(e) {
				this.streeValue = e.target.value;
			},
			doorInput:function(e) {
				this.doorValue = e.target.value;
			},
			// 添加地址
			add(){
				uni.request({
					url: this.http+'home/addAddress',
					method: 'POST',
					header: {
						authKey: this.authKey
					},
					data: {
						name:this.nameValue,
						phone:this.phoneValue,
						city:'吉林省延吉市',
						road:this.streeValue,
						subdistrict:this.doorValue
					},
					success: res => {
						if (res.data.code == 200) {
							this.getAdress();
							this.nameValue = ''
							this.phoneValue = ''
							this.streeValue = ''
							this.doorValue = ''
						}else{
							uni.showToast({
								title:res.data.error,
								icon:'none',
								duration:1500
							})
						}
					},
				});
			},
			choose(item){
				uni.setStorageSync('address',item)
				uni.navigateBack()
// 				uni.navigateTo({
// 					url: '/pages/reservationService/coldCalling?id=' + this.biId +'&jid=' +this.jid
// 				});
				
			}
		}
	}
